* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.load-more.is-hidden {
  display: none;
}

.search-btn, .load-more {
  color: #fff;
  cursor: pointer;
  border-radius: 100px;
  margin-left: 15px;
  padding: 12px 16px;
  font-size: 14px;
  transition: background-color .25s linear;
}

.search-btn:hover, .search-btn:focus {
  background-color: #b65cf2;
}

.load-more:hover, .load-more:focus {
  background-color: #69e63c;
}

.search-btn {
  background-color: #7545dc;
}

.load-more {
  background-color: #2dc95c;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.search-form {
  background-color: #2888f6;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  gap: 25px;
  margin-top: 30px;
  margin-bottom: 20px;
  margin-left: 30px;
  display: flex;
}

.photo-card {
  width: 100px;
  flex-basis: calc(25% - 25px);
}

.photo {
  width: 100%;
  height: 80%;
}

.info {
  width: 100%;
  letter-spacing: .03em;
  justify-content: space-between;
  gap: 5px;
  margin-top: auto;
  margin-bottom: 5px;
  font-size: 12px;
  line-height: 1.9;
  display: flex;
}

.info-item {
  flex-direction: column;
  margin-top: 0;
  display: flex;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: inline-flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-icon {
  fill: #000;
}

.modal {
  width: 335px;
  height: 762px;
  background: #fff;
  border: 2px solid #111;
  border-radius: 18px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)translateY(-50%);
}

.backdrop.is-hidden .modal {
  opacity: 0;
  transform: translateX(-50%)translateY(-70%);
}

.modal-form-title {
  color: #000;
  letter-spacing: .03em;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.15;
}

.photo {
  width: 287px;
  height: 408px;
}

.pop-info {
  margin: 40px 24px;
}

.pop_name {
  letter-spacing: -.04em;
  color: #111;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.pop_author {
  letter-spacing: -.04em;
  color: #b4afaf;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.pop_description {
  letter-spacing: -.04em;
  color: #111;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.pop_shop {
  justify-content: flex-start;
  gap: 20px;
  list-style: none;
  display: flex;
}

.pop_shop:not(:lang(ae)):not(:lang(ar)):not(:lang(arc)):not(:lang(bcc)):not(:lang(bqi)):not(:lang(ckb)):not(:lang(dv)):not(:lang(fa)):not(:lang(glk)):not(:lang(he)):not(:lang(ku)):not(:lang(mzn)):not(:lang(nqo)):not(:lang(pnb)):not(:lang(ps)):not(:lang(sd)):not(:lang(ug)):not(:lang(ur)):not(:lang(yi)) {
  padding-left: 0;
}

.pop_shop:is(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)) {
